import { transform, i18n, lan } from '../../unit/const'
import { isMobile } from '../../unit'

import Nebulas from 'nebulas'
import NebPay from 'nebpay.js'
import _ from 'lodash'


var Neb = Nebulas.Neb
// var neb = new Neb(new Nebulas.HttpRequest('https://testnet.nebulas.io'))
var neb = new Neb(new Nebulas.HttpRequest('https://mainnet.nebulas.io'))
var api = neb.api

//  test
// const dappAddress = 'n22J78th2oiX7eL8LS8hgE6n3msLvap3jzB'

//  main
const dappAddress = 'n1y9rqm7po8cbPyXHrxGPybAfLoUqRcZgyC'


export default {
  name: 'Guide',
  data() {
    return {
      isMobile: isMobile(),

      dialogRankingInfoVisible: false,
      nickName: '',
      highestScore: 0, // 
      chainScore: 0, // 
      gamerAddress: localStorage.getItem('gamerAddress') ? localStorage.getItem('gamerAddress') : '',
      rankingList: [], // 
      currentRow: null
    }
  },
  computed: {
    linkTitle: () => i18n.linkTitle[lan],
    github: () => i18n.github[lan],
    QRCode: () => i18n.QRCode[lan],
    QRTitle: () => i18n.QRNotice[lan],
    QRSrc: () =>
      window.location.protocol +
      '//binaryify.github.io/vue-tetris/static/qr.jpeg'
  },
  mounted() {
    window.addEventListener('resize', this.resize.bind(this), true)

    // 
    if (window.localStorage.getItem('VUE_TETRIS')) {
      this.highestScore = JSON.parse(decodeURIComponent(atob(window.localStorage.getItem('VUE_TETRIS')))).max;
    }
  },
  methods: {
    resize() {
      this.isMobile = isMobile()
    },
    inputNickName() {
      this.inquireChainScore()

      this.$prompt('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          inputPattern: /\S/,
          inputErrorMessage: ''
        }).then(({ value }) => {
          this.nickName = value
          // 
          this.toNasChain()
        }).catch(() => {

        });

      // this.$message({
      //   message: '',
      //   type: 'warning'
      // });
    },
    showLoading() {
      this.loading = this.$loading({
        lock: true,
        text: '... ',
        background: 'rgba(0, 0, 0, 0.8)'
      });
      setTimeout(() => {
        loading.close();
      }, 300000);
    },
    /**/
    inquireChainScore () {
      var from = ''
      if (!this.gamerAddress) {
        from = 'n1MuZcKoAQEx4446rPYWeVb7zxPqskS4tcv'
      } else {
        from = this.gamerAddress
      }
      var value = '0'
      var nonce = '0'
      var gas_price = '1000000'
      var gas_limit = '2000000'

      // 
      var contract = {
          "function": "getGamerScore",
          "args": ""
      }

      neb.api.call(
        from,
        dappAddress,
        value,
        nonce,
        gas_price,
        gas_limit,
        contract
      ).then( (resp) => {
            if (resp["result"] !== "null") {

              this.chainScore = JSON.parse(resp["result"]).score
              console.log("", this.chainScore)

            } else {

            }
        }).catch(function (err) {

            console.log("error:" + err.message)
        })

    },
    /**/
    showRanking () {
      this.dialogRankingInfoVisible = true

      var from = ''
      if (!this.gamerAddress) {
        from = 'n1MuZcKoAQEx4446rPYWeVb7zxPqskS4tcv'
      } else {
        from = this.gamerAddress
      }
      var value = '0'
      var nonce = '0'
      var gas_price = '1000000'
      var gas_limit = '2000000'

      // 
      var contract = {
          "function": "getRankingList",
          "args": ""
      }

      neb.api.call(
        from,
        dappAddress,
        value,
        nonce,
        gas_price,
        gas_limit,
        contract
      ).then( (resp) => {
            console.log("", resp)
            if (resp["result"] !== "null") {

              this.rankingList = JSON.parse(resp["result"]);
              // score
              if (this.rankingList.length) {
                this.rankingList = _.sortBy(this.rankingList, (obj, key) => {
                  return -obj.score
                })
              }

            } else {

            }
        }).catch(function (err) {

            console.log("error:" + err.message)
        })

    },
    // 
    toNasChain () {
      // 
      console.log(' score', this.highestScore);

      if (this.highestScore === 0) {
        this.$message({
          message: '0',
          type: 'warning'
        });
        return;
      }

      if(this.chainScore >= this.highestScore) {
        this.$message({
          message: '',
          type: 'warning'
        });
      } else {
        this.showLoading();

        // 
        var nebPay = new NebPay()

        var value = "0"
        var callFunction = "save"
        var callArgs =JSON.stringify([this.nickName, this.highestScore])
        console.log(callArgs)

        nebPay.call(
          dappAddress,
          value,
          callFunction,
          callArgs, {
            listener: this.cbResult
          }
        );

      }

    },
    /**/
    cbResult (response) {
      console.log("response callback: " + JSON.stringify(response))

      if (JSON.stringify(response) === '"Error: Transaction rejected by user"') {

        if (this.loading) {
          this.loading.close();
        }

        this.$notify({
          title: '',
          message: '',
          type: 'error'
        });

        return;
      }

      var intervalQuery = setInterval(() => {
        api.getTransactionReceipt({hash: response["txhash"]}).then((receipt) => {
            console.log("...", receipt)
            if (receipt.from) {
              this.gamerAddress = receipt.from;
              // localStorage
              localStorage.setItem('gamerAddress', receipt.from);
            }
            // console.log(receipt)
            if (receipt["status"] === 2) {
                console.log("pending.....")
            } else if (receipt["status"] === 1){

                if (this.loading) {
                  this.loading.close();
                }

                this.$notify({
                  title: '',
                  message: '',
                  type: 'success'
                })
                //
                clearInterval(intervalQuery)
            }else {
                console.log("......")
                if (this.loading) {
                  this.loading.close();
                }

                this.$notify({
                  title: '',
                  message: '',
                  type: 'warning'
                });


                //
                clearInterval(intervalQuery)
            }
        });
      }, 3000);

    }
  }
}



